  .toggle {
    position: relative;
    display: inline;
  
    input[type="checkbox"] {
      position: relative;
      width: 100px;
      height: 35px;
      background: #bdc3c7;
      -webkit-appearance: none;
      border-radius: 20px;
      outline: none;
      transition: 0.4s;
      box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
      cursor: pointer;
      padding-top: 0;
    }
  
    input:checked[type="checkbox"] {
      background: $principal;
    }
  
    input[type="checkbox"]::before {
      z-index: 2;
      position: absolute;
      content: "";
      left: 0;
      width: 40px;
      height: 35px;
      background: $gris;
      border-radius: 50%;
      transform: scale(1.1);
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
      transition: 0.4s;
    }
  
    input:checked[type="checkbox"]::before {
      left: 60px;
    }
  
    label {
      position: absolute;
      color: #fff;
      font-weight: 600;
      font-size: 20px;
      pointer-events: none;
    }
  
    .onbtn {
      bottom: 15px;
      left: 15px;
    }
  
    .ofbtn {
      bottom: 15px;
      right: 5px;
      color: #34495e;
    }
  }


  #hidden{
    display: none;
}
  
